import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout_v2 from "@components/layout_v2";
import Seo from "@components/seo";
import { useLocation } from "@reach/router";
import HireSocialMediaManagerScreen from "@screens/hiringPages/hire-social-media-manager";
import BreadCrumbV2 from "@components/common/breadcrumb-v2";

const HireSocialMediaManager = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Hire Social Media Manager", link: null },
  ];
  const { data } = props;
  const location = useLocation();
  const fileUrl =
    data?.hireSocialMediaManagerImg?.childImageSharp?.gatsbyImageData?.images
      ?.fallback?.src;
  const validData = data?.allMetaJson?.nodes[0];
  return (
    <Layout_v2 db={db}>
      <Seo
        title={validData?.title}
        metaTitle={validData?.metaTitle}
        metaKeyword={validData?.metaKeyword}
        description={validData?.description}
        image={fileUrl}
        siteFirestore={data?.site?.siteMetadata?.firestore}
        slug={location.pathname.split("/")?.[1]}
      ></Seo>
      <BreadCrumbV2 items={breadcrumbLinks} />
      <main>
        <HireSocialMediaManagerScreen db={db} />
      </main>
    </Layout_v2>
  );
};

export default HireSocialMediaManager;
export const queryData = graphql`
  query MyQuery {
    hireSocialMediaManagerImg: file(
      relativePath: { eq: "hire-social-media-manager.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allMetaJson(filter: { slug: { eq: "hire-social-media-manager" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
